}

.customerRow {
  padding: 10px 15px;
  border: 1px solid grey;
  background: var(--fifth);
  font-size: 14px;
  min-width: 250px;
}

.customerAction {
  margin-left: 10px;
  cursor: pointer;
  float: right;
}

.customerReportHeading {
  font-weight: 600;
  font-size: 18px;
  margin-bottom: 10px;
}

.customerReportAddNew {
  border: 1px solid grey;
  background: var(--fifth);
  font-size: 14px;
  min-width: 250px;
}


